import { Icon as Iconify } from "@iconify/react";
import { LoadingButton } from "@mui/lab";
import { Box, Container, Divider, ListItem, ListItemButton, ListItemIcon, ListItemText, SvgIcon } from "@mui/material";
import { useSnackbar } from "notistack";
import { Fragment, useState } from "react";
import { useMiddleware } from "../../../../Application";
import FormSection from "../../../../components/FormSection";
import SingleFieldDialog from "../../../../components/SingleFieldDialog";
import TextField from "../../../../components/TextField";
import useForm from "../../../../utils/useForm";

const Security = () => {
    const middleware = useMiddleware('Authentication.Authenticated');

    const { enqueueSnackbar } = useSnackbar();
    const { data, handleSubmit, getValue, setValue, reset, loading } = useForm({
        initialValues: {
            password: '',
            password_confirmation: ''
        },
        handlePost: data => middleware.changePassword(data),
        onSubmit: () => {
            enqueueSnackbar('رمز عبور تغییر یافت.');
            reset();
        }
    })

    const [fieldDialogProps, setFieldDialogProps] = useState({ open: false });
    const openField = (renderContent, options = {}) => {
        return () => {
            setFieldDialogProps({
                renderContent,
                ...options,
                open: true,
            });
        };
    };

    const fieldDefs = [
        // {
        //     label: 'رمز عبور فعلی',
        //     defaultValue: getValue("password") || "-",
        //     onClick: openField(
        //         ({ value, setValue }) => (
        //             <TextField
        //                 InputLabelProps={{ shrink: true }}
        //                 fullWidth={true}
        //                 label="رمز عبور فعلی"
        //                 variant="filled"
        //                 value={value}
        //                 onChange={(e) => setValue(e.target.value)}
        //             />
        //         ),
        //         {
        //             title: " رمز عبور فعلی",
        //             defaultValue: { firstName: getValue("firstName"), lastName: getValue("lastName") },
        //             onSubmit: (current) => changeAndSubmit({ current }),
        //         }
        //     )
        // },
        {
            label: 'رمز عبور جدید',
            icon: "solar:lock-keyhole-minimalistic-bold-duotone",
            defaultValue: getValue("password") ? '********' : "-",
            onClick: openField(
                ({ value: _value, setValue: _setValue }) => (
                    <TextField
                        type="password"
                        InputLabelProps={{ shrink: true }}
                        fullWidth={true}
                        label="رمز عبور جدید"
                        variant="filled"
                        value={_value}
                        onChange={(e) => _setValue(e.target.value)}
                        autoFocus
                        InputProps={{ dir: 'ltr' }}
                    />
                ),
                {
                    title: " رمز عبور جدید",
                    onSubmit: (value) => {
                        setValue('password', value);
                        setFieldDialogProps({ open: false });
                    },
                }
            )
        },
        {
            label: 'تکرار رمز عبور جدید',
            icon: "solar:lock-password-bold-duotone",
            defaultValue: getValue("password_confirmation") ? '********' : "-",
            onClick: openField(
                ({ value: _value, setValue: _setValue }) => (
                    <TextField
                        type="password"
                        InputLabelProps={{ shrink: true }}
                        fullWidth={true}
                        label="تکرار رمز عبور جدید"
                        variant="filled"
                        value={_value}
                        onChange={(e) => _setValue(e.target.value)}
                        autoFocus
                        InputProps={{ dir: 'ltr' }}
                    />
                ),
                {
                    title: "تکرار رمز عبور جدید",
                    onSubmit: (value) => {
                        setValue('password_confirmation', value);
                        setFieldDialogProps({ open: false });
                    },
                }
            )
        },
    ]

    return (
        <>
            <Container maxWidth="lg">
                <FormSection
                    title="تغییر رمز عبور"
                    description="تنظیمات مرتبط با رمز عبور که برای ورود به حساب کاربری استفاده می‌شود."
                >
                    {fieldDefs.map((field, index) => (
                        <Fragment key={index}>
                            <ListItem disableGutters disablePadding>
                                <ListItemButton
                                    selected
                                    onClick={field.onClick}
                                >
                                    <ListItemIcon>
                                        <SvgIcon component={Iconify} icon={field.icon} />
                                    </ListItemIcon>
                                    <ListItemText
                                        primary={field.defaultValue}
                                        secondary={field.label}
                                    />
                                </ListItemButton>
                            </ListItem>

                            {index < fieldDefs.length && <Box sx={{ my: 1 }} />}
                        </Fragment>
                    ))}

                    <Box sx={{ my: 2 }} />

                    <LoadingButton
                        variant="contained"
                        size="large"
                        loading={loading}
                        onClick={handleSubmit}
                    >تغییر رمز عبور</LoadingButton>
                </FormSection>
                <Divider />
            </Container>

            <SingleFieldDialog
                {...fieldDialogProps}
                onClose={() => setFieldDialogProps({ open: false })}
            />
        </>
    );
}

export default Security;